.switch-checkbox {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  visibility: none;
  display: none;
}

.slider-round {
  width: 40px;
  height: 20px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #4b48db;
  border-radius: 20px;
  transition: background-color .25s, border .25s;
  display: block;
  position: relative;
  overflow: hidden;
}

.slider-round:before {
  content: "";
  width: 16px;
  height: 16px;
  background-color: #4b48db;
  border-radius: 50%;
  transition: transform .25s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switch-checkbox:checked ~ .slider-round {
  background-color: #4b48db;
  border-color: #fff;
}

.switch-checkbox:checked ~ .slider-round:before {
  background-color: #fff;
  transform: translateX(20px);
}

